.App {
  background: linear-gradient(106.37deg,
   #e6f594 29.63%,
    #e6be83 51.55%,
    #f793e6 90.85%);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Inter',sans-serif;
}
.AppGlass{
  display: grid;
  height: 97%;
  width: 97%;
  border-radius: 2rem;
  background-color: rgba(255, 255, 255, 0.4); 
  overflow: hidden;
  grid-template-columns: 11rem auto 20rem;
}
.sidebar{
  display: flex;
  
}